<template>
  <div class="page">
    <el-breadcrumb separator="/" style="margin-bottom: 50px;">
      <el-breadcrumb-item to="/integral">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/integral/goodsCreate">商品分类</el-breadcrumb-item>
      <el-breadcrumb-item>创建</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form
      :inline="true"
      ref="form"
      @submit.native.prevent="onSubmit"
      :model="form"
      :rules="rules"
      class="form-search"
    >
      <el-row>
        <el-col>
          <el-form-item label="分类名称" prop="name">
            <el-input size="small" placeholder="分类名称" v-model="form.name"> </el-input>
            <el-input size="small" placeholder="分类名称（英文）" v-model="form.name_en"> </el-input>
            <el-input size="small" placeholder="分类名称（越南)" v-model="form.name_yn"> </el-input>
          </el-form-item>
        </el-col>
        <!-- <el-col>
          <el-form-item label="分类说明" prop="image">
            <el-input size="small" placeholder="请输入说明" v-model="form.image"> </el-input>
            <el-input size="small" placeholder="请输入说明" v-model="form.image_en"> </el-input>
          </el-form-item>
        </el-col> -->
        <el-col>
          <el-button native-type="submit" type="primary">提交</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
export default {
  name: 'typeEdit',
  components: {},
  data() {
    return {
      form: {
        name: '',
        name_en: '',
        name_yn: '',
        image: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          {
            min: 1,
            max: 30,
            message: '长度在 3 到 30 个字符',
            trigger: 'blur',
          },
        ],
        image: [
          { required: true, message: '请输入分类说明', trigger: 'blur' },
          {
            min: 1,
            max: 30,
            message: '长度在 3 到 30 个字符',
            trigger: 'blur',
          },
        ],
      },
      is_open_v: false,
      config: {
        status: {},
        type: {},
        is_low_risk: null,
        cats: null,
      },
      file: [],
      file1: [],
      date: new Date(),
      id: null,
    }
  },
  mounted() {
    this.id = this.$route.query.id
    var url = '/goods/cat/getlist'
    if (this.id) {
      url = '/goods/cat/edit/' + this.id
      Fetch2(url).then(r => {
        if (r.data.view) {
          this.form = r.data.view
          this.config = r.data.config
        } else {
          this.config = r.data.config
        }
      })
    } else {
      Fetch(url, { id: this.id }).then(r => {
        if (r.data.view) {
          this.form = r.data.view
          this.config = r.data.config
        } else {
          this.config = r.data.config
        }
      })
    }
  },
  methods: {
    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          const id = this.$router.history.current.query.id
          var url = '/goods/cat/create'
          if (id) {
            url = '/goods/cat/edit/' + id
            Fetch3(url, {
              id: this.$router.history.current.params.id,
              ...this.form,
            }).then(() => {
              Message.success('操作成功')
              this.$router.replace('/integral/type')
            })
          } else {
            Fetch(url, {
              id: this.$router.history.current.params.id,
              ...this.form,
            }).then(() => {
              Message.success('操作成功')
              this.$router.replace('/integral/type')
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
  },
}
</script>
<style lang="less" scoped>
.page {
  padding: 0 0 40px;
}

.form-search {
  max-width: 1000px;

  /deep/ .el-input {
    width: 300px;
  }

  /deep/ .el-form-item__label {
    width: 140px;
    text-align: right;
  }
}
</style>
